@use "sass:math";

$trophy-size: 15vw;

.trophies {
	display: flex;
	justify-content: space-evenly;
	height: 30vh;
	padding: 15px $trophy-size;
}

.trophy {
	max-width: 0;
	overflow: visible;
	position: relative;
}

.trophy img {
	position: absolute;
	top: 0;
	left: math.div(-$trophy-size, 2);
	max-width: $trophy-size;
	width: 100vw;
}